import { IConfig } from '../domain/interfaces/IConfig';
import { ILogger } from '../domain/interfaces/ILogger';
import { IHealthChecker } from '../domain/interfaces/IHealthChecker';
import { ILogProcessor } from '../domain/interfaces/ILogProcessor';
import { ISdkProvider } from '../domain/interfaces/ISdkProvider';
import { ICorsValidator } from '../domain/interfaces/ICorsValidator';
import { IAuthValidator } from '../domain/interfaces/IAuthValidator';
import { IRateLimiter } from '../domain/interfaces/IRateLimiter';

import { ConsoleLogger } from '../infrastructure/logger/ConsoleLogger';
import { HealthHandler } from '../infrastructure/handlers/HealthHandler';
import { LogHandler } from '../infrastructure/handlers/LogHandler';
import { SdkHandler } from '../infrastructure/handlers/SdkHandler';
import { CorsValidator } from '../infrastructure/middleware/CorsValidator';
import { AuthValidator } from '../infrastructure/middleware/AuthValidator';
import { InMemoryRateLimiter } from '../infrastructure/middleware/RateLimiter';

export interface IWorkerContainer {
  getLogger(): ILogger;
  getHealthChecker(): IHealthChecker;
  getLogProcessor(): ILogProcessor;
  getSdkProvider(): ISdkProvider;
  getCorsValidator(): ICorsValidator;
  getAuthValidator(): IAuthValidator;
  getRateLimiter(): IRateLimiter;
}

export class WorkerContainer implements IWorkerContainer {
  private logger: ILogger;
  private healthChecker: IHealthChecker;
  private logProcessor: ILogProcessor;
  private sdkProvider: ISdkProvider;
  private corsValidator: ICorsValidator;
  private authValidator: IAuthValidator;
  private rateLimiter: IRateLimiter;

  constructor(config: IConfig) {
    // Initialize all dependencies
    this.logger = new ConsoleLogger(config.environment);
    this.healthChecker = new HealthHandler(config);
    this.logProcessor = new LogHandler(this.logger);
    this.sdkProvider = new SdkHandler();
    this.corsValidator = new CorsValidator(config.corsOrigins, config.enableCorsWildcard);
    this.authValidator = new AuthValidator(config.apiKey);
    this.rateLimiter = new InMemoryRateLimiter(
      config.rateLimitMaxRequests,
      config.rateLimitWindowMs
    );
  }

  getLogger(): ILogger {
    return this.logger;
  }

  getHealthChecker(): IHealthChecker {
    return this.healthChecker;
  }

  getLogProcessor(): ILogProcessor {
    return this.logProcessor;
  }

  getSdkProvider(): ISdkProvider {
    return this.sdkProvider;
  }

  getCorsValidator(): ICorsValidator {
    return this.corsValidator;
  }

  getAuthValidator(): IAuthValidator {
    return this.authValidator;
  }

  getRateLimiter(): IRateLimiter {
    return this.rateLimiter;
  }
}