import { IHealthChecker } from '../../domain/interfaces/IHealthChecker';
import { IConfig } from '../../domain/interfaces/IConfig';

export class HealthHandler implements IHealthChecker {
  private config: IConfig;
  private version: string;

  constructor(config: IConfig, version = '1.0.0') {
    this.config = config;
    this.version = version;
  }

  async checkHealth(): Promise<{
    status: string;
    timestamp: string;
    environment: string;
    version?: string;
  }> {
    return {
      status: 'healthy',
      timestamp: new Date().toISOString(),
      environment: this.config.environment,
      version: this.version
    };
  }
}