import { ILogProcessor } from '../../domain/interfaces/ILogProcessor';
import { ILogger } from '../../domain/interfaces/ILogger';
import { LogEntry, LogLevel, LogMetadata } from '../../domain/types';

export class LogHandler implements ILogProcessor {
  private logger: ILogger;

  constructor(logger: ILogger) {
    this.logger = logger;
  }

  async processLogs(logs: any[], request: Request): Promise<LogEntry[]> {
    if (!Array.isArray(logs)) {
      throw new Error('Logs must be an array');
    }

    const processedLogs: LogEntry[] = [];
    
    for (const log of logs) {
      if (!this.validateLogEntry(log)) {
        this.logger.warn('Invalid log entry', { log });
        continue;
      }

      const entry: LogEntry = {
        ts: log.ts || new Date().toISOString(),
        source: 'browser',
        level: (log.level || 'info') as LogLevel,
        msg: log.msg || log.message || '',
        meta: log.meta || {}
      };

      const enrichedEntry = this.enrichWithMetadata(entry, request);
      processedLogs.push(enrichedEntry);
      
      // Echo to worker logs
      this.logger.info(JSON.stringify(enrichedEntry));
    }

    return processedLogs;
  }

  validateLogEntry(entry: any): boolean {
    if (!entry || typeof entry !== 'object') {
      return false;
    }
    
    // Must have at least a message or msg field
    if (!entry.msg && !entry.message) {
      return false;
    }

    // Validate level if provided
    if (entry.level) {
      const validLevels = ['debug', 'info', 'warn', 'error'];
      if (!validLevels.includes(entry.level)) {
        return false;
      }
    }

    return true;
  }

  enrichWithMetadata(entry: LogEntry, request: Request): LogEntry {
    const enrichedMeta: LogMetadata = {
      ...entry.meta,
      rayId: request.headers.get('CF-Ray') || undefined,
      origin: request.headers.get('Origin') || undefined,
      userAgent: request.headers.get('User-Agent') || undefined,
      ip: request.headers.get('CF-Connecting-IP') || undefined
    };

    // Clean up undefined values
    Object.keys(enrichedMeta).forEach(key => {
      if (enrichedMeta[key] === undefined) {
        delete enrichedMeta[key];
      }
    });

    return {
      ...entry,
      meta: enrichedMeta
    };
  }
}