import { IWorkerContainer } from '../container/WorkerContainer';

export class Router {
  private container: IWorkerContainer;

  constructor(container: IWorkerContainer) {
    this.container = container;
  }

  async handleRequest(request: Request): Promise<Response> {
    const url = new URL(request.url);
    const path = url.pathname;
    const logger = this.container.getLogger();

    logger.info(`Incoming request: ${request.method} ${path}`);

    try {
      switch (path) {
        case '/api/health':
          return await this.handleHealth();

        case '/api/log':
          if (request.method !== 'POST') {
            return this.methodNotAllowed();
          }
          return await this.handleLog(request);

        case '/sdk.js':
          return await this.handleSdk();

        default:
          return this.notFound();
      }
    } catch (error) {
      logger.error('Request handling error', { error: error.message, path });
      return this.serverError();
    }
  }

  private async handleHealth(): Promise<Response> {
    const healthChecker = this.container.getHealthChecker();
    const health = await healthChecker.checkHealth();
    
    return new Response(JSON.stringify(health), {
      status: 200,
      headers: {
        'Content-Type': 'application/json'
      }
    });
  }

  private async handleLog(request: Request): Promise<Response> {
    const logProcessor = this.container.getLogProcessor();
    
    try {
      const logs = await request.json();
      await logProcessor.processLogs(logs, request);
      
      return new Response(JSON.stringify({ success: true }), {
        status: 200,
        headers: {
          'Content-Type': 'application/json'
        }
      });
    } catch (error) {
      return new Response(JSON.stringify({ 
        error: 'Invalid request body',
        message: error.message 
      }), {
        status: 400,
        headers: {
          'Content-Type': 'application/json'
        }
      });
    }
  }

  private async handleSdk(): Promise<Response> {
    const sdkProvider = this.container.getSdkProvider();
    const content = await sdkProvider.getSdkContent();
    
    return new Response(content, {
      status: 200,
      headers: {
        'Content-Type': sdkProvider.getContentType(),
        'Cache-Control': 'public, max-age=3600'
      }
    });
  }

  private methodNotAllowed(): Response {
    return new Response(JSON.stringify({ error: 'Method not allowed' }), {
      status: 405,
      headers: {
        'Content-Type': 'application/json'
      }
    });
  }

  private notFound(): Response {
    return new Response(JSON.stringify({ error: 'Not found' }), {
      status: 404,
      headers: {
        'Content-Type': 'application/json'
      }
    });
  }

  private serverError(): Response {
    return new Response(JSON.stringify({ error: 'Internal server error' }), {
      status: 500,
      headers: {
        'Content-Type': 'application/json'
      }
    });
  }
}