import { ISdkProvider } from '../../domain/interfaces/ISdkProvider';

export class SdkHandler implements ISdkProvider {
  private sdkContent: string;
  private version: string;

  constructor() {
    this.version = '1.0.0';
    // This will be replaced with actual SDK content once built
    this.sdkContent = this.getPlaceholderSdk();
  }

  async getSdkContent(): Promise<string> {
    return this.sdkContent;
  }

  getSdkVersion(): string {
    return this.version;
  }

  getContentType(): string {
    return 'application/javascript';
  }

  private getPlaceholderSdk(): string {
    return `// StackScope SDK v${this.version}
// This will be replaced with the actual built SDK
(function() {
  console.log('[StackScope] SDK placeholder loaded. Please build and deploy the actual SDK.');
})();`;
  }

  updateSdkContent(content: string): void {
    this.sdkContent = content;
  }
}